  margin: 10rem 35rem;
}
.userinfo{
  display: flex;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  align-items: center;
  
}
.userinfo p,.userinfo h3{
  margin-left: -6rem;
  
}

.userinfo label{
  margin-left: -14rem;
}
.userinfo input{
  padding: 0.8rem;
  width: 57%;

  margin-bottom: 2rem;

}
button{
  padding: 0.8rem;
  width: 63%;

  margin-bottom: 2rem;
}
.userinfo button{
  background-color: #625BF7;
  color: white;
  
}
.userMessage{
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
height: 0;

}
.Login{
  width: 30%;
  margin:10rem;
